import Vue from 'vue';
import Router from 'vue-router';
import $ from 'jquery';
import config from  "../../static/config.js";
Vue.use(Router);
let  rout={};
let comp="Main";
$.ajax({
	type: "post",
	url: config.serverUrl+"api/Module/GetAllModules",
	data: config.systemID+"",
	cache: false,
	contentType:"application/json",
	async: false,
	success: function (res) {
	let data = res;
	let pageRoutes = [];
	data.filter((ele)=>{
								
							 	if(ele.component && ele.component != ""){
							 			let obj = {
									 			path:"/"+ele.path,
									 			name:ele.component,
									 			
												//component: resolve => require([`../components/page/${ele.component}.vue`], resolve),
												//利用webpack.ensure做的按需加载
									 			component : r => require.ensure( [], () => r (require(`../components/page/${ele.component}.vue`))),
									 			meta:{
									 				title:ele.name,
									 				id:ele.id,
									 			}
									 		};
							 			if(ele.helper){
							 				var val = ele.component.split("/");
							 				var urlArr= val.filter((o)=>{
							 					
							 					return o!="" && o 
							 				})
							 				let n = [];
							 				if(urlArr.length<4){
												const un = urlArr.reverse();
							 				    n =[
							 					       un[2] || "components",
							 					       un[1] || "controler",
							 					       un[0] 
							 					];
							 					
							 				}
							 				delete obj.component;
							 				obj.components={

							 					default : r => require.ensure( [], () => r (require('../components/page/Main.vue'))),
							                    helper : r => require.ensure( [], () => r (require(`../${n[0]}/${n[1]}/${n[2]}.vue`))),
							             
							 				}
								 			
							 			}
							 		if(ele.name == "permission"){
							 			obj.meta.permission = true;
							 		}
							 		pageRoutes.push(obj);
							 		
							 };				
	})
	
							pageRoutes.push({
			                    path: '/PostalCode',
			                    name:"PostalCode",
			                    component: r => require.ensure( [], () => r (require('Components/page/PostalCode.vue'))),
			                    meta: { title: '政区代码' }
			                },
			                {
			                    path: '/SkywayCesium',
			                    name:"SkywayCesium",
			                    component:r => require.ensure( [], () => r (require('Components/page/SkywayCesium.vue'))),
			                    meta: { title: '三维电子沙盘' }
			                },
							{
			                    path: '/SubcompartmentWorkSpace',
			                    name:"SubcompartmentWorkSpace",
			                    component:r => require.ensure( [], () => r (require('Components/page/SubcompartmentWorkSpace.vue'))),
			                    meta: { title: '小班工作空间' }
			                });
							rout = {
										mode:'hash', //history需要服务器端配置
										base: '/SmartForest.Frame.VueManage/',   
									    routes: [
									    	{
									    		path: '/',
            									redirect: '/Main',
									    	},{
									        	path: '/',
									            component:r => require.ensure( [], () => r (require('../components/page/Header/Home.vue'))),
									            meta: { title: '自述文件' },
									            children:pageRoutes,
									       },
									       {
							                    path: '/404',
							                    component:r => require.ensure( [], () => r (require('Components/common/vue/404.vue'))),
							                    meta: { title: '404' }
							                },
							                {
							                    path: '/403',
							                    component:r => require.ensure( [], () => r (require('Components/common/vue/403.vue'))),
							                    meta: { title: '403' }
							                },
									    	{
									            path: '/login',
									            component:r => require.ensure( [], () => r (require('../components/page/Login.vue'))),
									        },
									        {
									            path: '*',
									            redirect: '/404'
									        },
									    ],
								}
	},
	error:function(){
		rout = {
				mode:'hash',
			    routes: [
			        {
			            path: '/',
			            redirect: '/Main',
			        },
			        {
			            path: '/',
			            component:r => require.ensure( [], () => r (require('Components/page/Header/Home.vue'))),
			            meta: { title: '自述文件' },
			            children:[
			                {
			                    path: '/Main',
			                    component:r => require.ensure( [], () => r (require(`../components/page/${comp}.vue`))),
			                    name:"Main",
			                    meta: { 
			                    	title: '系统首页' ,
			                    //	permission: true
			                    },
			                },
			                {
			                    		path: "SubcompartmentBuild",
			                    		name:"SubcompartmentBuild",
					                    components:{
					                    	default:r => require.ensure( [], () => r (require(`../components/page/${comp}.vue`))),
					                    	helper:r => require.ensure( [], () => r (require('../components/controler/SubcompartmentBuild.vue'))),
					                    	
					                    },
					                    meta: { 
					                    	title: '小班编辑' ,
					                    //	permission: true
					                    },
			                    		
			                },
			                {
			                    path: '/ProjectOverview',
			                    name:"ProjectOverview",
			                    component:r => require.ensure( [], () => r (require('Components/page/ProjectOverview.vue'))),
			                    meta: { title: '工程概况' }
			                },
			                  {
			                    path: '/ForestDesign',
			                    name:"ForestDesign",
			                    component:r => require.ensure( [], () => r (require('Components/page/ForestDesign.vue'))),
			                    meta: { title: '造林设计' }
			                },
			                 {
			                    path: '/ForestApproval',
			                    name:"ForestApproval",
			                    component:r => require.ensure( [], () => r (require('Components/page/ForestApproval.vue'))),
			                    meta: { title: '工程审核' }
			                },
			                 {
			                    path: '/ForestAccept',
			                    name:"ForestAccept",
			                    component:r => require.ensure( [], () => r (require('Components/page/ForestAccept.vue'))),
			                    meta: { title: '工程验收' }
			                },
			                 {
			                    path: '/ForestCheck',
			                    name:"ForestCheck",
			                    component:r => require.ensure( [], () => r (require('Components/page/ForestCheck.vue'))),
			                    meta: { title: '工程核查' }
			                },
			                {
			                    path: '/ForestPlan',
			                    name:"ForestPlan",
			                    component:r => require.ensure( [], () => r (require('Components/page/ForestPlan.vue'))),
			                    meta: { title: '造林计划' }
			                },
			                {
			                    path: '/ForestMode',
			                    name:"ForestMode",
			                    component:r => require.ensure( [], () => r (require('Components/page/ForestMode.vue'))),
			                    meta: { title: '造林模式' }
			                },
			                {
			                    path: '/PostalCode',
			                    name:"PostalCode",
			                    component:r => require.ensure( [], () => r (require('Components/page/PostalCode.vue'))),
			                    meta: { title: '政区代码' }
			                },
			                {
			                    path: '/EconomicsIndex',
			                    name:"EconomicsIndex",
			                    component:r => require.ensure( [], () => r (require('Components/page/EconomicsIndex.vue'))),
			                    meta: { title: '经济指标' }
			                },
			                {
			                    path: '/SubcompartmentWorkSpace',
			                    name:"SubcompartmentWorkSpace",
			                    component:r => require.ensure( [], () => r (require('Components/page/SubcompartmentWorkSpace.vue'))),
			                    meta: { title: '小班编辑工作空间' }
			                },
			                {
			                    path: '/404',
			                    component:r => require.ensure( [], () => r (require('Components/common/vue/404.vue'))),
			                    meta: { title: '404' }
			                },
			                {
			                    path: '/403',
			                    component:r => require.ensure( [], () => r (require('Components/common/vue/403.vue'))),
			                    meta: { title: '403' }
			                },
			            ]
			        },
			        {
			            path: '/login',
			            component:r => require.ensure( [], () => r (require('Components/page/Login.vue'))),
			        },
			        {
			            path: '*',
			            redirect: '/404'
			        }
			    ]
			}
	}
})
console.log(rout);
export default new Router(rout);


